.tireService {
    text-align: center;
    padding: 50px 0 40px;
    p {
        text-align: left;
        font-size: 20px;
    }
    img {
        max-width: 100%;
        height: auto;
        max-height: 500px;
    }
}

@media screen and (max-width: 1200px) {
    .tireService {
        p {
            font-size: 18px;
        }

    }
}

@media screen and (max-width: 992px) {
    .tireService {
        p {
            font-size: 16px;
        }

    }
}

@media screen and (max-width: 576px) {
    .tireService {
        p {
            font-size: 14px;
        }

    }
}